import React from 'react';
import { render, fireEvent } from '@testing-library/react-native';
import Modal from '../../../src/components/ui/Modal';
import { Text } from 'react-native';

describe('Modal', () => {
  it('ne devrait pas rendre le contenu si isVisible est faux', () => {
    const { queryByText } = render(
      <Modal isVisible={false} onClose={() => {}}>
        <Text>Contenu du modal</Text>
      </Modal>
    );
    expect(queryByText('Contenu du modal')).toBeNull();
  });

  it('devrait rendre le contenu si isVisible est vrai', () => {
    const { getByText } = render(
      <Modal isVisible={true} onClose={() => {}}>
        <Text>Contenu du modal</Text>
      </Modal>
    );
    expect(getByText('Contenu du modal')).toBeTruthy();
  });

  it('devrait appeler onClose quand le fond est pressé et backdropDismiss est vrai', () => {
    const mockOnClose = jest.fn();
    const { getByTestId } = render(
      <Modal isVisible={true} onClose={mockOnClose} backdropDismiss={true}>
        <Text>Contenu du modal</Text>
      </Modal>
    );
    fireEvent.press(getByTestId('modal-backdrop'));
    expect(mockOnClose).toHaveBeenCalledTimes(1);
  });

  it('ne devrait pas appeler onClose quand le fond est pressé et backdropDismiss est faux', () => {
    const mockOnClose = jest.fn();
    const { queryByTestId } = render(
      <Modal isVisible={true} onClose={mockOnClose} backdropDismiss={false}>
        <Text>Contenu du modal</Text>
      </Modal>
    );
    fireEvent.press(queryByTestId('modal-backdrop'));
    expect(mockOnClose).not.toHaveBeenCalled();
  });

  it('devrait rendre le titre si fourni', () => {
    const { getByText } = render(
      <Modal isVisible={true} onClose={() => {}} title="Titre du Modal">
        <Text>Contenu</Text>
      </Modal>
    );
    expect(getByText('Titre du Modal')).toBeTruthy();
  });
});
